import axios from 'axios';
import React,{useEffect,useRef} from 'react'

 function LineChart() {


const canvasRef= useRef(null)

   
// eslint-disable-next-line
useEffect(async()=>{
const ctx = canvasRef.current.getContext('2d')
const {data} =await axios.get('/admin/dashboard/chart')
const myChart = new window.Chart(ctx, {
    type: 'line',
    data: {
        labels: ['Monday', 'Tuesday', 'Wendsday', 'Thursday', 'Friday', 'Saturday'],
        datasets: [{
            label: '# of Votes',
            data: data.sales,
            backgroundColor: [
                'rgba(255, 99, 132, 0.2)',
                'rgba(54, 162, 235, 0.2)',
                'rgba(255, 206, 86, 0.2)',
                'rgba(75, 192, 192, 0.2)',
                'rgba(153, 102, 255, 0.2)',
                'rgba(255, 159, 64, 0.2)'
            ],
            borderColor: [
                'rgba(255, 99, 132, 1)',
                'rgba(54, 162, 235, 1)',
                'rgba(255, 206, 86, 1)',
                'rgba(75, 192, 192, 1)',
                'rgba(153, 102, 255, 1)',
                'rgba(255, 159, 64, 1)'
            ],
            borderWidth: 1
        }]
    },
    options: {
        scales: {
            y: {
                beginAtZero: true
            }
        }
    }
});

return () => {
    myChart.destroy()
 }

},[])




    return (
        <div>
           <canvas height='300' ref={canvasRef}></canvas>
 
        </div>
    )
}

export default LineChart;
